<template>
  <div class="dewbackTopContainer"
    :class="classBackTop"
    :style="stylesBackTop"
    @click="handleBtnBackTopClick"
  >
    <DewIcon v-if="!hasSlot"
      :svgName="svgName"
      :iconName="iconName"
      :size="iconSize"
      :color="color"
    />
    <div v-else>
      <slot></slot>
    </div>
  </div>
</template>

<script>
import {
  animationBackTopFn // 
} from '../../../utils/assistFunction.js'
import {
  addListener,
  removeListener
} from '../../../utils/eventListener.js' // 
import DewIcon from '../../icon/index.js'

const prefixEsClassName = 'dew-back-top' //  class , 

export default {
  name: 'DewBackTop',
  data () {
    return {
      isBackTop: false // , false , true 
    }
  },
  props: {
    // ,  30px
    bottom: { type: [Number, String], default: 30 },
    // ,  30px
    right: { type: Number, default: 30 },
    // 
    duration: { type: Number, default: 1000 },
    //   ,  400 px
    height: { type: Number, default: 0 },
    backgroundColor: { type: String, default: null },
    color: { type: String, default: null },
    iconName: { type: String, default: 'to-top' },
    svgName: { type: String, default: '' },
    iconSize: [Number, String]
  },
  computed: {
    hasSlot () {
      return !!this.$slots.default
    },
    classBackTop () {
      return [
        `${prefixEsClassName}`, // dew-back-top
        {
          [`${prefixEsClassName}-show`]: this.isBackTop,
          [`${prefixEsClassName}-noSlot`]: !this.hasSlot,
          hasSlot: this.hasSlot
        }
      ]
    },
    stylesBackTop () {
      const styleObj = {}
      styleObj.right = `${this.right}px`
      styleObj.bottom = `${this.bottom}px`
      if (this.backgroundColor) {
        styleObj['background-color'] = this.backgroundColor
      }
      if (this.color) {
        styleObj.color = this.color
      }
      return styleObj
    }
    //
  },
  components: { DewIcon },
  beforeDestroy () {
    //  window.addEventListener  , ,  ( dom  window  document)
    removeListener(window, 'scroll', this.isBackTopFn, { passive: true, capture: false })
    removeListener(window, 'scroll', this.isBackTopFn, { passive: true, capture: false })
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.addEventListener()
    },
    addEventListener () {
      //  window.addEventListener  , ,  ( dom  window  document)
      addListener(window, 'scroll', this.isBackTopFn, { passive: true, capture: false })
      addListener(window, 'resize', this.isBackTopFn, { passive: true, capture: false })
    },
    // (),   ,  400 px
    isBackTopFn () {
      //   height,   pageYOffset > 0  ,  >= height  
      this.height === 0 ? this.isBackTop = window.pageYOffset > 0 : this.isBackTop = window.pageYOffset >= this.height
    },
    handleBtnBackTopClick () {
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      animationBackTopFn(window, scrollTop, 0, this.duration)
      this.$emit('on-click', scrollTop) //  click ,     
    }
    //
  }
  //
}
</script>

<style scoped>
.dewbackTopContainer {
  cursor: pointer;
}
  .dew-back-top {
    position: fixed;
    /* right  bottom   props  right  bottom  */
    cursor: pointer;
    display: none;
    z-index: 42;
  }
  /* v-show ,  className    */
  .dew-back-top-show {
    display: block;
  }
  .dew-back-top-noSlot {
    box-sizing: border-box;
    padding: 6px 10px;
    border-radius: 4px;
    font-size: 16px;
    color: #fff;
    box-shadow: 0 1px 3px rgba(0, 0, 0,.2);
    background-color: rgba(0, 0, 0,.6);
  }
  .hasSlot {
    padding: 10px;
    border-radius: 4px;
    font-size: 12px;
    text-align: center;
    color: #fff;
    background-color: rgba(46, 58, 41, .6);
  }
</style>
